@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&family=Noto+Serif+JP&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Noto Serif JP', serif;
  letter-spacing: 3px;
}

/* bubbles */
@keyframes move {
  100% {
      transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}
.background span {
  width: 50vmin;
  height: 50vmin;
  border-radius: 50vmin;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  animation: move;
  animation-duration: 45;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  z-index: 10;
}
.background span:nth-child(0) {
  color: rgba(255, 253, 209, 0.2);
  top: 2%;
  left: 51%;
  animation-duration: 37s;
  animation-delay: -21s;
  transform-origin: 21vw 11vh;
  box-shadow: -100vmin 0 12.756131537979913vmin currentColor;
}
